import { Component, Input,  } from '@angular/core';
@Component({
  selector: 'crem-dropdown',
  template: `
   <div class="dx-fieldset">
    <div class="dx-field">
        <div class="dx-field-label">{{label}}</div>
        <div [style.width]="width" class="dx-field-value">
            <dx-select-box #selectBox [items]="products" [value]="products[0]" [disabled]="disabled" [placeholder]="placeholder">
            </dx-select-box>
        </div>
    </div>
    <div class="current-value">
        Selected product is <span>{{selectBox.value}}</span>
    </div>
</div>
  `,
  styleUrls: ['dropdown.component.scss']
})
export class DropdownComponent {

  @Input()
  public label: string;

  @Input()
  public placeholder: string;

  @Input()
  public disabled: boolean = false;

  @Input()
  public width?: string = "250px";

  @Input()
  public products? : any[] = ['product 1', 'product 2', 'product 3'];



  constructor( ) {
}
}
